
import { defineComponent, ref } from 'vue'
import { loadingService } from '@/shared/services/LoadingScreenService'

const loading = new loadingService()

export default defineComponent({
    name: "apm202002sc00",
    data() {
        const dataTable = ref()
        const tempDataTable = ref()
        const header = [{ columnName: "customerNo", sortable: true, resizeAble: true, }, { columnName: "authorizationLineOption" }, { columnName: "authorizationLinePolicyTypeCode", sortable: true, resizeAble: true, alignment: 'start' }, { columnName: "authorizationLineAssignTypeCode", sortable: false }, { columnName: "customerName", sortable: false },{columnName:"authorizationLineSetupCount"}];
        return {
            dataTable,
            header,
            roleIdSearch: "",
            statusSearch: "All",
            tempDataTable
        }
    },
    created() {
        this.requestData()
    },
    methods: {
        async requestData() {
            loading.openloading()
            const body = {
                customerName: "",
                pageSize: 0,
                authorizationLinePolicyTypeCode: "",
                customerNo: ""

            }
            const table: any = await this.$request.getRequest('APM20100311', body, true)
            if (table) {
                loading.closeLoading()
            }
            this.dataTable = table?.body?.list
        },
        handleRegister() {
            this.$router.push('/common-management/ib-role/register')
        },
        handleRowClick(index, data) {
            this.$router.push(`/common-management/ib-role/edit/${data.id}`)
        },
        async handleSearchRequest() {
            loading.openloading()
            const body = {
                roleId: this.roleIdSearch,
                status: this.statusSearch
            }
            const table: any = await this.$request.getRequest('COM20700331', body, true)
            if (table) {
                this.dataTable = table?.body?.roleList
                loading.closeLoading()
            }
        },
    }
})
